.App {
  font-family: "./Font/FontsFree-Net-SFProDisplay-Regular.ttf";
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.BorderMaker {
  border-color: #ffffff;
  border: 7px solid rgb(255, 255, 255);
}


.MainText {
  font-size: 100px;
  display: flex;
  color: rgb(200, 200, 200); 
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  align-items: center;
}

@font-face {
  font-family: "MyFont";
  src: local("MyFont"),
    url(./Font/FontsFree-Net-SFProDisplay-Regular.ttf) format("ttf");
}

.App-header {
  background-color: #1e2127;
  min-height: 100vh;
  flex-direction: column;
  align-items: flex-start;
  justify-content: start;
  font-size: calc(10px + 2vmin);
  color: white;

  font-family: "./Font/FontsFree-Net-SFProDisplay-Regular.ttf";
}

.Brief {
  text-align: center;
  margin-top: 95px;
  font-style: initial;
  font-variant: normal;
  font-weight: 900;
}

.whatIDo {
  text-align: center;
  text-align: center;
  font-size: 95px;
  font-weight: 900;
  font-family: Arial, Helvetica, sans-serif;
}

.Big {
  display: flex;
  font-size: xxx-large;
  font-variant: normal;
  margin-bottom: 95px;
  margin-top: 95px;
}

.aboutme {
  display: inline-block;
  font-size: 80px;
  font-weight: bolder;
}

.codeLineNumber {
  font-size: 15px;
  font-family: monospace;
  width: 0.1%;
  color: #ffffff;
}


.codeTableTd {
  width: 0.1%;
}

.marquee {
  font-weight: 200;
  font-size: 55px;
  font-style: oblique;
  color: black;
  -webkit-text-fill-color: #1e2127; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}

.App-link {
  color: #61dafb;
}

