.page-header {
  background-position: center center;
  background-size: cover;
  min-height: 100vh;
  max-height: 999px;
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: 1;

  &.page-header-small {
    min-height: 65vh !important;
    max-height: 700px;
  }
  &.page-header-xs {
    min-height: 40vh !important;
  }

  .page-header-image {
    position: absolute;
    background-size: cover;
    background-position: center center;
    width: 45%;
    height: 100%;
    z-index: -1;
  }

  .content-bottom-right{
      position: absolute;
      bottom: 8px;
      right: 16px;
      font-size: 18px;
  }

  .filter {
    &::after {
      background-color: rgba(0, 0, 0, 0.5);
      content: "";
      display: block;
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
      z-index: 2;
    }
  }

  

  
}
